main.realtime-growth {
  display: flex;
  justify-content: center;
}

main.realtime-growth section.realtime-content {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 5vw 3vw;
  width: 90%;
  height: 40vw;
  font-family: "Poppins";
}

main.realtime-growth section.realtime-heading {
  display: flex;
  justify-content: space-between;
}

main.realtime-growth section.realtime-heading h1 {
  margin-top: 3vw;
  font-size: var(--webHeading2);
  font-weight: 700;
}

main.realtime-growth section.realtime-heading p {
  margin-top: 3vw;
  width: 80%;
  font-size: var(--webNormalFont);
}

main.realtime-growth section.realtime-info {
  display: flex;
  justify-content: space-between;
  font-size: 1.2vw;
}

section.realtime-info div.card {
  display: block;
  flex: 1 0 0;
  margin: 0 0.6vw;
  padding: 3vw 0;
}

img.icon {
  max-height: 15vw;
}

img.icon-small {
  width: 7vw;
}

img.realtime-bg-img {
  width: 100%;
  opacity: 0;
}
